import { CHAT_RESPONSE_SOURCES_CLASS_NAME, INTERACTION_CLASS_NAME } from "@rp/react/web/chat"
import { Box, alpha, styled } from "@rp/react/web/mui/core"

export const WatchlistChatContainer = styled(Box)(({ theme, minHeight }) => {
  const { dp01 } = theme.palette.elevation
  const transparent = alpha(dp01, 0)
  return {
    flexGrow: 1,
    [`& .${INTERACTION_CLASS_NAME}`]: { paddingBottom: 8 },
    [`& .${INTERACTION_CLASS_NAME}:last-of-type`]: {
      minHeight,
    },
    [`& .${CHAT_RESPONSE_SOURCES_CLASS_NAME}`]: {
      paddingLeft: theme.spacing(2),
      paddingRight: theme.spacing(2),
    },
    [`& .swiper-arrow-wrapper:first-of-type`]: {
      left: 0,
    },
    [`& .swiper-arrow-wrapper svg`]: {
      backgroundColor: dp01,
    },
    [`& .swiper-arrow-wrapper:before`]: {
      background: `linear-gradient(-90deg, ${dp01} 0%, ${transparent} 100%)`,
    },
    [`& .swiper-arrow-wrapper:after`]: {
      background: `linear-gradient(90deg, ${dp01} 0%, ${transparent} 100%)`,
    },
  }
})
